import React, { Component } from 'react';
import InitialState from '../../../data/initialState';
import Typist from 'react-typist';

class TypedMessage extends Component {
    
    constructor(props){
        super(props)
        this.state = { typed: InitialState.sections.appBanner.typedText, currentTyped: 0, typing: true }
    }

    done = () => {
        var nextState;
        if(this.state.currentTyped >= this.state.typed.length - 1) {
            nextState = 0
        }
        else {
            nextState = this.state.currentTyped + 1
        }
        this.setState({ typing: false, currentTyped: nextState }, () => {
            this.setState({ typing: true })
        });
    }

    render() {
        return (
            <div>
            { this.state.typing
                ? <Typist onTypingDone={this.done}><u>{this.state.typed[this.state.currentTyped]}</u><Typist.Delay ms={1000} /><Typist.Backspace count={this.state.typed[this.state.currentTyped].length} /></Typist>
                : null
            }
            </div>
        )
    }
}

export default TypedMessage;


