import React, { Component } from 'react';
import InitialState from '../../data/initialState';

class Feed extends Component {
    constructor(props) {
        super(props)
        this.state = { feeds: InitialState.sections.feeds }
    }

    render(){
        return (
            <section className="text-center feed-section" id="feeds">
                <div className="container">
                <h3></h3>
                <h1 className="mb-5 text-accent"><i className="fas fa-broadcast-tower pulse"></i> &nbsp;Current Feed</h1>
                <p className="text-left">I like to attend workshops and meetups, it helps me meet new people and also learn a lot through their experiences. I also get to know about new tech and brush up my existing skills in the technologies that I use.</p>
                <h3 className="text-left mt-5">Recent Workshops</h3>
                <hr className="hr-almost-black pad-0 margin-0 mb-2"></hr>
                <div className="row">
                    {this.state.feeds.map((feed,i) =>
                        <div className="col-12 col-md-4 pad-1 text-black text-left" key={feed.title}>
                            <h4>{feed.title}</h4>
                            <p>{feed.text}</p>
                            <p className="margin-0"><b>Venue:</b> {feed.venue}</p>
                            <p><b>Date:</b> {feed.date}</p>
                            <a href={feed.link} target="_blank" rel="noopener noreferrer"><i className="fas fa-external-link-alt"></i> Link</a>
                        </div>
                    )}
                </div>
                </div>
            </section>
        )
    }
}

export default Feed;


