import React, { Component } from 'react';
import InitialState from '../../data/initialState';

class Projects extends Component {
    constructor(props){
        super(props);
        this.state = { projects: InitialState.sections.projects }
    }

    render() {
        return(
        <section className="bg-off-white" id="projects">
            
            <h1 className="text-accent"><i className="fas fa-project-diagram"></i> &nbsp;Projects</h1>
            <div className="container">
                <div className="row mt-5">
                {this.state.projects.map((project) => { 
                    return (
                    <div className="col-12 col-md-6 hover-card pad-2 sr-icons" key={project.title}>
                        <h3 className="text-left">{project.title}</h3>
                        <hr className="hr-almost-black"></hr>
                        <p className="text-accent text-left margin-0">Descripition :</p>
                        <ul>
                        { project.text.map((textBullet)=>
                            <li className="text-left margin-0" key={textBullet.length}>
                                {textBullet}
                            </li>
                        )}
                        </ul>
                        <p className="text-left text-grey-dark margin-0 mt-3"><i className="fas fa-calendar-alt text-accent"></i>&nbsp; {project.date}</p>
                        <p className="text-left text-grey-dark"><i className="fas fa-code text-accent"></i>&nbsp; {project.technologies}</p>  
                    </div>
                    ) 
                })}
                </div>
                <h5 className="text-center">and more...</h5>
            </div>
        </section>
        )
    }
}

export default Projects;


