import React, { Component } from 'react';

class Footer extends Component {

    render(){
        return(
            <footer>
                <section className="text-off-white text-center bg-almost-black footer-section">
                    <p className="margin-0">
                        <a href="https://www.linkedin.com/in/deepankarjaisia/" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin-in app-icon-base pad-0-50 text-grey"></i></a>
                        <a href="https://github.com/deepjaisia" target="_blank" rel="noopener noreferrer"><i className="fab fa-github app-icon-base pad-0-50 text-grey"></i></a>
                        <a href="mailto:dj1029@nyu.edu" target="_blank" rel="noopener noreferrer"><i className="fas fa-envelope app-icon-base pad-0-50 text-grey"></i></a>
                    </p>
                    
                    <p className="margin-0">
                        Made with &nbsp;<i className="fas fa-heart" style={{ color: "red" }}></i>&nbsp; using&nbsp;&nbsp;
                        <i className="fab fa-react" style={{ color: "#61dafb", fontSize: "125%" }}></i>&nbsp;&nbsp;
                        <i className="fab fa-node-js" style={{ color: "#026e00", fontSize: "125%" }}></i>&nbsp;&nbsp;
                        <i className="fab fa-sass" style={{ color: "#b37399", fontSize: "125%" }}></i>&nbsp;&nbsp;
                        
                    </p>  
                </section>
            </footer>
        )
    }
}

export default Footer;


