import React, { Component } from 'react';
import InitialState from '../../data/initialState';
import smileGif from '../../assets/blobSmile.gif';

class AboutMe extends Component {

    constructor(props) {
        super(props)
        this.state = { education: InitialState.sections.aboutMe.education, experience: InitialState.sections.aboutMe.experience, funFacts: InitialState.sections.aboutMe.funFacts }
    }
    render() {
        return(
            <section className="bg-accent about-me" id="aboutMe">
                <div className="container">
                <h1 className="mb-5"><i className="fas fa-user"></i> &nbsp;About Me</h1>
                <p className="text-left text-off-white">
                    Front-End Engineer who has a keen interest in developing scalable Web Applications that have a seamless user-experience while serving a purpose.
                    I like solving problems of whichever degree it is ranging from user problems to coding problems and even puzzles. I like to challenge myself on a
                    daily basis and learn something new that helps me become a better programmer and an individual. &nbsp;
                    <img style={{ height: "1.5rem", width: "1.5rem" }} src={smileGif} alt="Smiling GIF" />
                </p>
                <div className="row mt-5 text-center">
                    <div className="col-12 col-md-6 col-xl-4 mt-3 text-left">
                        <h3 className="mb-4"><i className="fas fa-briefcase"></i> &nbsp;{this.state.experience.title}</h3>
                        { this.state.experience.list.map((listItem) =>
                                <div key={listItem.subTitle}>
                                    <p className="text-off-white margin-0">{listItem.title}</p>
                                    <p className="margin-0">{listItem.subTitle}</p>
                                    <p className="">{listItem.date}</p>
                                    
                                </div>
                        )}
                    </div>
                    <div className="col-12 col-md-6 col-xl-4 mt-3 text-left">
                        <h3 className="mb-4"><i className="fas fa-university"></i> &nbsp;{this.state.education.title}</h3>
                        { this.state.education.list.map((listItem) =>
                                <div key={listItem.title}>
                                    <p className="text-off-white margin-0">{listItem.title}</p>
                                    <p className="margin-0">{listItem.subTitle}</p>
                                    <p className="">{listItem.date}</p>
                                    
                                </div>
                        )}
                    </div>
                    <div className="col-12 col-md-6 col-xl-4 mt-3 text-left">
                        <h3 className="mb-4"><i className="fab fa-superpowers"></i> &nbsp;{this.state.funFacts.title}</h3>
                        <ul>
                        { this.state.funFacts.list.map((listItem) =>
                                <li key={listItem}>
                                    <p className="text-off-white mb-0">{listItem}</p>
                                </li>
                        )}
                        </ul>
                    </div>
                </div>
                </div>
            </section>
        )
    }
}

export default AboutMe;


