import React, { Component } from 'react';
import InitialState from '../../data/initialState';

class Skill extends Component {

    constructor(props) {
        super(props)
        this.state = { skillSections: InitialState.sections.skills }
    }

    render() {
        return (
            <section className="text-center skill-section" id="skills">
                <h4>
                    <i className="fas fa-moon"></i>
                    <i className="fas fa-sun"></i>
                </h4>
                <div className="container">
                <h1 className="mb-5 text-accent">
                    Skills
                </h1>
                <div className="row">
                    { this.state.skillSections.map((section,i) => 
                        <div className="col-12 col-lg-4 color-animator pad-1" key={section.title}>
                            <h3 className="mb-2">{section.title}</h3>
                            <ul className="pad-0 margin-0" style={{ listStyle: "none" }}>
                                { section.list.map((item,j) => <li key={item}><p className="margin-0">{item}</p></li> ) }
                            </ul>
                        </div>
                    )
                    }
                </div>
                </div>
            </section>
        )
    }
}

export default Skill;


