import React, {Component} from 'react';
import AppBanner from './App/Sections/Banner/AppBanner.jsx';
import Footer from './App/Footer';
import Projects from './App/Sections/Projects.jsx';
import AboutMe from './App/Sections/AboutMe.jsx';
import Skill from './App/Sections/Skill.jsx';
import Feed from './App/Sections/Feed.jsx';
import Navbar from './App/Navbar';
import './App.css';

class App extends Component {

  render() {
    return (
      <div className="text-center" style={{ marginTop: "-8px" }}>
        <Navbar />
        <AppBanner />
        <Projects />
        <AboutMe />
        <Feed />
        <Skill />
        <Footer />
      </div>
    );
  }
}

export default App;


