import React,{ Component } from 'react';
import TypedMessage from './TypedMessage.jsx';
import Scene from './Scene.js';

class AppBanner extends Component {

    render() {
        return(
            <section className="portfolio-banner" style={{ display: "flex", alignItems: "center" }} id="top">
              <div className="container">
              <div className="row">
                {/* <div className="col-12 text-center">
                    <Scene />
                </div> */}
                <div className="col-12 col-md-6">
                <h1 className="text-off-white text-center text-md-right">
                  I am
                </h1>
                </div>
                <div className="col-12 col-md-6">
                <h1 className="text-black-70 text-center text-md-left">
                    <TypedMessage />
                </h1>
                </div>
                <div className="col-12 mt-5 mb-3">
                    <a data-scroll href="#projects"><button style={{ boxShadow: "1.75px 1.75px 1.25px 1.25px" }} className="btn btn-accent">Learn More</button></a>
                </div>
              </div>
              <i style={{ bottom:0, position: "absolute", visibility: "visible" }} className="fas fa-2x fa-angle-double-down mb-5 scroll-down"></i>
              </div>
            </section>
        )
    }
}

export default AppBanner;


